.popup-btn {
  font-size: 32px;
  color: white;
  letter-spacing: 3px;
  position: fixed;
  top: 40%;
  left: -20px;
  margin-left: -35px;
  -webkit-transform: rotate(-270deg);
          transform: rotate(-270deg);
  text-decoration: none;
  transition: all 0.5s ease;
  border: 5px solid white;
  border-radius: 15px;
  padding: 15px;
}

.popup-btn:hover {
  margin-left: 0;
  color: white;

  text-decoration: underline;
}

.popup {
  position: fixed;
  padding: 10px;
  width: 500px;
  border-radius: 10px;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: rgb(179, 179, 179);
  visibility: hidden;
  opacity: 0;
  /* "delay" the visibility transition */
  transition: opacity 0.5s, visibility 0s linear 0.5s;
  z-index: 1;
}
.popup:target {
  visibility: visible;
  opacity: 1;
  /* cancel visibility transition delay */
  transition-delay: 0s;
}
.popup-close {
  position: absolute;
  padding: 10px;
  width: 500px;
  border-radius: 10px;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: rgba(255, 255, 255, 0.9);
}
.popup .close {
  position: absolute;
  right: 5px;
  top: 5px;
  padding: 10px;
  color: #000;
  transition: color 0.3s;
  font-size: 2em;
  line-height: 0.6em;
  font-weight: bold;
}
.popup .close:hover {
  color: #c90000;
}

.close-popup {
  background: rgba(0, 0, 0, 0.7);
  cursor: default;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  visibility: hidden;
  /* "delay" the visibility transition */
  transition: opacity 0.5s, visibility 0s linear 0.5s;
}
.popup:target + .close-popup {
  opacity: 1;
  visibility: visible;
  /* cancel visibility transition delay */
  transition-delay: 0s;
}

